<template>
  <div class="fill-height">
    <app-bar title="Palavra do presidente">
      <template v-slot:append>
        <v-btn icon to="/" replace>
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
      </template>
    </app-bar>
    <async-container :loading="!loaded">
      <v-card
        max-width="680"
        class="mx-auto mb-7"
        :dark="$store.state.dark"
      >
        <v-card-text>
          <p class="text-justify" style="white-space: pre-wrap">{{texto}}</p>
          <div class="text-center">
            <p>O futuro chegou. Venha conosco!</p>
            <img v-if="foto" :src="foto" class="img-fluid presidente" alt="">
            <p class="title mb-0">{{nome}}</p>
            <p class="blue--text subtitle-2 mb-0">Presidente do COFECI</p>
          </div>
        </v-card-text>
      </v-card>
    </async-container>
  </div>
</template>

<script>
  import AppWebClient from '@/http/AppWebClient';
  import ImageHelper from '@/helpers/ImageHelper';
  import AsyncContainer from '@/components/AsyncContainer';
  import AppBar from '@/components/AppBar';
  export default {
    name: 'PalavraPresidenteCofeci',
    components: {AppBar, AsyncContainer},
    data: () => ({
      loaded: false,
      texto: '',
      nome: '',
      foto: null,
    }),
    methods: {
      async loadData() {
        try {
          const appWebClient = new AppWebClient(true);
          const data = await appWebClient.getPresidente(0);
          this.texto = data.texto;
          this.nome = data.nome;
          const blob = await appWebClient.getPresidenteFoto(0);
          this.foto = await ImageHelper.blobToDataURI_async(blob);
        } catch (e) {
          // eslint-disable-next-line no-console
          console.log(e);
          let dados = localStorage.getItem('presidentecofeci');
          if (dados) {
            dados = JSON.parse(dados);
            this.texto = dados.texto;
            this.nome = dados.nome;
            this.foto = dados.foto;
          }
          else await this.$router.push('/');
        } finally {
          this.loaded = true;
        }
      },
    },
    created() {
      this.loadData();
    },
    beforeDestroy() {
      if (this.loaded) {
        localStorage.setItem('presidentecofeci', JSON.stringify({
          texto: this.texto,
          nome: this.nome,
          foto: this.foto,
          uf: this.uf,
        }));
      }
    },
  }
</script>

<style scoped>
  .presidente {
    border-radius: 50%;
    width: 8rem;
    height: 8rem;
    object-fit: cover;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,.16), 0 2px 10px 0 rgba(0,0,0,.12);
  }
</style>